  height: 20px;
  border-radius: 50%;
  background-color: #30ca71;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
  transform: scale(0);
