    text-align: center;
  }
  .footer-btn a {
    display: block;
    margin: 0 0 10px;
  }
  .footer-btn a:first-child {
    margin-top: 0;
  }
  .acr-footer .footer-logo {
    margin: 0 auto 20px;
  }
  .acr-footer .social-media {
    justify-content: center;
  }
  .acr-language-selector > a {
    display: inline-flex;
    justify-content: center;
  }
  .footer-bottom .row {
    flex-direction: column-reverse;
  }
  .footer-bottom ul {
    flex-direction: column;
    margin: 0 0 30px;
  }
  .footer-bottom ul li + li {
    margin: 10px 0 0;
  }
  .post .post-controls {
    flex-direction: column-reverse;
    align-items: flex-start;
  }
  .post .post-controls > a {
    display: block;
    width: 100%;
  }
  .post-comment-authors {
    margin-bottom: 15px;
  }
  .sidebar-widget .listing.listing-list .listing-thumbnail {
    width: 100px;
  }
  .acr-area-price-wrapper > h5 {
    display: none;
  }
  .acr-listing-history img {
    margin-bottom: 10px;
  }
  .listing-feature h6 {
    font-size: 14px;
  }
  .listing-feature .listing-feature-label {
    width: 120px;
  }
  .listing-feature i {
    width: 55px;
  }
  .acr-cs-container .acr-cs-content,
  .acr-auth-container .acr-auth-content {
    padding: 40px 15px;
  }
  .acr-cs-content-footer {
    display: block;
  }
  .acr-cs-content-footer .social-media {
    justify-content: center;
    margin-top: 20px;
  }
  .section-title-wrap.flex-header {
    display: block;
  }
  .section-title-wrap.flex-header .acr-arrows,
  .section-title-wrap.flex-header .btn-custom {
    margin-top: 15px;
  }
  .section-title-wrap.flex-header .acr-arrows .slick-arrow {
    margin-left: 0;
    margin-right: 10px;
  }
  .acr-locations {
    padding: 20px;
  }
  .cta.cta-2 {
    display: block;
    text-align: center;
    padding: 20px;
  }
  .cta.cta-2 > i {
    margin-right: 0;
    margin-bottom: 20px;
  }
}
